import React, {useState, useEffect} from 'react';
import logo from './logo.svg';
import './App.css';

function App() {
  const [text, setText] = useState('default text')

  useEffect(() => {
    fetch('/text').then((res) => (res.text())).then((text) => {
      setText(text)
    });
  }, [])
  return (
    <div className="App">
      <div className='Message'>
        {text}
      </div>
    </div>
  );
}

export default App;
